    import React, { Suspense, useEffect, useState } from "react";
    import { Card, CardBody } from "react-bootstrap";
    import TopPerfomingSkeleton from "../../../layout/skeleton/topPerfomingSkeleton";
    import NoData from "../../../layout/common/noData";
    import { kFormatter } from "../../../../../../utils";
    import {
    getPrfomingPostMore,
    getreactedpostsMore,
    } from "../../../../../../actions/analyticsNew";
    import SectionHeading from "../../../layout/common/sectionHeading";
    import { Splide, SplideSlide, SplideTrack } from "@splidejs/react-splide";
    import PercentDifference from "../../../layout/common/percentDifference";
    import AuthorInfo from "../../../content/components/authorInfo";
    import PopupModal from "../../../layout/common/popup/popupModal";
    import PostMedia from "../../../layout/common/postMedia";
    import Sentiment from "../../../../dashboard-new/components/common/sentiment";
    const PostPopup = React.lazy(() => import("../../../layout/popup/postPopup"));
    const PostdetailPopup = React.lazy(() =>
    import("../../../layout/popup/postdetailPopup")
    );

    const PerfomingPost = (props) => {
    const { type, title, postData, loader, totalCount, selectedWallFeed, isTrial } = props;

    const [currentIndex, setCurrentIndex] = useState(0);

    const [show, setShow] = useState(false);
    const [showPost, setshowPost] = useState(false);
    const [postInfo, setpostInfo] = useState(false);
    const [updateSlider, setupdateSlider] = useState(0);

    const handlePopup = (event) => {
        setShow(event);
    };

    const handlePostpopup = (event) => {
        setshowPost(event);
    };

    const handleResize = () => setupdateSlider(Math.random())

    useEffect(() => {
        window.addEventListener("resize", handleResize);
  
        return () => {
            window.removeEventListener("resize", handleResize);
        };
      }, []);
  


    const GetPostdata = (value, valueIndex) => {
        const e = value.data;
        const index = value.indexValue;

        return (
        <div className="top_posts top_posts_new">
            <div className="card-shadow rounded mb-1">
            <div className="m_card_cont">

             <div
                className="post_img1 cursor-pointer position-relative"
                onClick={() => {
                    setpostInfo(e);
                    handlePostpopup(true);
                }}
                >
                <PostMedia
                    key={index}
                    content={e.content}
                    mediaType={e.type}
                    postFile={e.postFile}
                    postId={e.id}
                    className={"img-fluid rounded-1"}
                />
                
                </div>  

                <div className="text-center position-relative m_user_info">
                <div className="top-perfom-post m_grid_author d-flex justify-content-center mb-3">
                    <AuthorInfo
                    // isIcon={e.network.icon}
                    picture={e.authorImage}
                    authorUsername={e.authorName}
                    authorName={e.authorName}
                    date={e.created}
                    link={e.link}
                    networkId={e.networkId}
                    />
                </div>
                </div>

                <div
                class="mt-4"
                onClick={() => {
                    setpostInfo(e);
                    handlePostpopup(true);
                }}
                >
                {type == 1 ? (
                    <div class="d-flex align-items-center justify-content-between py-2">
                    <span class="text-dark font-weight-bold font-size-md">
                        Impressions
                    </span>
                    <p class="text-dark font-weight-boldest mb-0 d-flex">
                        {e.postPerformance ? kFormatter(Math.round(e.postPerformance)) : 0}
                        <PercentDifference
                        difference={e.difference}
                        flag={e.flag}
                        />
                    </p>
                    </div>
                ) : null}

                <div class="d-flex align-items-center justify-content-between py-2">
                    <span class="text-dark font-weight-bold font-size-md">
                    Post {type == 1 ? "Clicks" : "Likes"}
                    </span>
                    <p class="text-dark font-weight-boldest mb-0 d-flex">
                    {type != 1 ? (
                        <svg
                        className="me-1"
                        xmlns="http://www.w3.org/2000/svg"
                        height="16"
                        width="16"
                        viewBox="0 0 24 24"
                        fill="#ff273c"
                        >
                        <path d="M12.001 4.52853C14.35 2.42 17.98 2.49 20.2426 4.75736C22.5053 7.02472 22.583 10.637 20.4786 12.993L11.9999 21.485L3.52138 12.993C1.41705 10.637 1.49571 7.01901 3.75736 4.75736C6.02157 2.49315 9.64519 2.41687 12.001 4.52853Z"></path>
                        </svg>
                    ) : null}
                    {type != 1 ? kFormatter(e.reactions) : kFormatter(e.postClicks)}
                    {type == 1 ? (
                        <PercentDifference
                        difference={e.difference}
                        flag={e.flag}
                        />
                    ) : null}
                    </p>
                </div>

                <div class="d-flex align-items-center justify-content-between pt-2">
                    <span class="text-dark font-weight-bold font-size-md me-5">
                    Sentiment
                    </span>
                    <div className="text-end">
                      <Sentiment sentiment={e.sentiment} />
                    </div>
                </div>
                </div>
            </div>
            </div>
        </div>
        );
    };
   
      
      

    const totalPost = postData?.length || 0;
    const canSlide = totalPost > 5;
    const perPagePost = canSlide ? 5 : totalPost;

    const createBreakpointSettings = (perPage,clones,postCount) => ({
    perPage,
    clones: clones,
    autoplay: postCount > perPage,
    drag: postCount > perPage,
    loop: postCount > perPage,
    arrows: postCount > perPage,
    });

    const settings = {
    type: "slide",
    rewind: false,
    speed: 1000,
    //clones: canSlide ? perPagePost : 0,
    perMove: 1,
    loop: false,
    interval: 5000,
    drag: totalPost>1 ? canSlide : false,
    perPage: perPagePost,
    gap: 0,
    autoplay: canSlide,
    pagination: false,
    arrows: canSlide,
    fixedHeight: true,
    breakpoints: {
        0: createBreakpointSettings(1, perPagePost, totalPost),
        579: createBreakpointSettings(1, perPagePost, totalPost),
        767: createBreakpointSettings(totalPost> 2 ? 2 : totalPost,0, totalPost),
        1300: createBreakpointSettings(totalPost> 3 ? 3 : totalPost,0, totalPost),
        2200: createBreakpointSettings(totalPost> 4 ? 4 : totalPost,0, totalPost),
     },
    };


    return (
        <div className="pb-5">
        <Card className="sw-card-custom card-shadow performing-post">
            <SectionHeading
            isTrial={isTrial}
            selectedWallFeed={selectedWallFeed}
            section={2}
            title={title}
            total={totalCount}
            handlePopup={handlePopup}
            />
            <CardBody className="p-5">
            <div className="top_posts_row row">
                {loader ? (
                <TopPerfomingSkeleton />
                ) : postData && Object.keys(postData).length ? (
               
                    <Splide key={updateSlider} hasTrack={false} options={settings}>
                    <SplideTrack>
                        {postData.map((e, index) => {
                        return (
                            <SplideSlide key={index + type}>
                            <GetPostdata
                                data={e}
                                indexValue={index}
                                key={index}
                            />
                            </SplideSlide>
                        );
                        })}
                    </SplideTrack>
                    </Splide>
              
                ) : (
                <NoData />
                )}
            </div>
            </CardBody>
        </Card>

    
          <PopupModal
            size={"xl"}
            showHide={show}
            handlePopup={handlePopup}
            title={`Top ${props.type == 1 ? `Perfoming` : `Reacted`} Posts`}
            >
            <Suspense fallback={<></>}>
               <PostPopup
                total={totalCount}
                moreData={
                    props.type == 1 ? getPrfomingPostMore : getreactedpostsMore
                }
                {...props}
                />
            </Suspense>
            </PopupModal>
     

        <PopupModal
            size={"lg"}
            showHide={showPost}
            handlePopup={handlePostpopup}
            title={`General Information`}
        >
            <Suspense fallback={<></>}>
            <PostdetailPopup {...postInfo} />
            </Suspense>
        </PopupModal>
        </div>
    );
    };

    export default React.memo(PerfomingPost);
